






























import { Component, Vue} from 'vue-property-decorator'
import AlbumItem from '../components/AlbumItem.vue'
import { IAlbum, IArea } from "../interfaces/types"
import * as request from "../services/request"
import "../assets/styles/style.css"

@Component({
  components: {
    AlbumItem,
  }
})
export default class QqMusic extends Vue {
  private areas:IArea[] = []
  private albums:IAlbum[] = []
  private currentArea:number | null = null

  private async load() {
    let [areas, albums] = await Promise.all([
        request.get<IArea[]>("data/areas.json"),
        request.get<IAlbum[]>("data/albums.json"),
      ]);
      this.areas = areas
      this.albums = albums
      this.currentArea = areas[0].id
  }

  private changeTab(id: number){
    this.currentArea = id;
  }

  private deleteAlbum(album: IAlbum) {
      const index = this.albums.indexOf(album)
      this.albums.splice(index, 1)
  }

  get currentAlbums(){
    return this.albums.filter((album) => album.area === this.currentArea)
  }

  private created(){
    this.load()
  }
}
