import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import QqMusic from '../views/QqMusic.vue'

Vue.use(VueRouter)

const routes: Array<RouteConfig> = [
  {
    path: '/',
    name: 'qqmusic',
    component: QqMusic
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
